
import { defineComponent, ref } from "vue";
import type { PFM201001SC00_RES_DETAIL, PFM201001SC00_REQ_REGISTER, PFM201001SC01_REQ_UPDATE } from "@/shared/type/PFM201001SC01";
import '@vuepic/vue-datepicker/dist/main.css'
import VueDatePicker from '@vuepic/vue-datepicker';
import { modalController } from "@ionic/vue";
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";
import { screenMode } from "@/shared/type/Approval.type";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";

export default defineComponent({
    name: "pfm201001sc01",

    components: {
        VueDatePicker,
        ModifyCard,
        BreadCrumb,
        SelectOption
    },

    data() {
        const feeBasicInfo = ref<PFM201001SC00_RES_DETAIL>({
            iSAccountCode: "",
            bSAccountCode: "",
            productFeeCode: "",
            feeCurrencyCode: "",
            applyStartDate: new Date(),
            applySeqNo: "",
            feeDescription: "",
            feeStartTime: { hours: 0, minutes: 0, seconds: 0 },
            feeEndTime: { hours: 0, minutes: 0, seconds: 0 },
            applyEndDate: new Date(),
            feeApplyStatusCode: "",
            productFeeTypeCode: "",
            productFeeBusinessTypeCode: "",
            onlineManualTypeCode: "",
            feeName: "",
            feeShortName: "",
            feeCalcTargetCode: "",
            feeCalcMethodTypeCode: "",
            feeCalcTargetAmountTypeCode: "",
            feeCustomerInputTypeCode: "",
            feeAccountNoInputTypeCode: "",
            feeCalcApplyBaseCode: ""
        });
        const modifyCard = {
            registerTellerId: "",
            registerTellerName: "",
            registerDate: "",
            registerTime: "",
            updatedTellerId: "",
            updatedTellerName: "",
            updatedDate: "",
            updatedTime: "",
            deleteTellerId: '',
            deleteTellerName: '',
            deleteDate: '',
            deleteTime: '',
        };
        const currentTime = new Date().toString().slice(15, 25)

        return {
            isEdit: true,
            isDisabledField: true,
            isMissingFiled: false,
            feeBasicInfo,
            isInvalidDate: false,
            isInvalidCode: false,
            currentTime,
            modifyCard,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any
        };
    },

    async mounted() {
        if (this.approvalType === '') {
            if (this.$route.query.type === 'register') {
                // this.feeBasicInfo.productFeeTypeCode = this.$store.state.CommonCodeModule.commonCode.ProductFeeTypeCode[0].key;
                // this.feeBasicInfo.feeCalcTargetAmountTypeCode = this.$store.state.CommonCodeModule.commonCode.FeeCalcTargetAmountTypeCode[0].key;
                this.feeBasicInfo.feeCurrencyCode = this.$store.state.CommonCodeModule.commonCode.FeeCurrencyCode[0].key;
                // this.feeBasicInfo.productFeeBusinessTypeCode = this.$store.state.CommonCodeModule.commonCode.ProductFeeBusinessTypeCode[0].key;
                // this.feeBasicInfo.feeCustomerInputTypeCode = this.$store.state.CommonCodeModule.commonCode.FeeCustomerInputTypeCode[0].key;
                // this.feeBasicInfo.onlineManualTypeCode = this.$store.state.CommonCodeModule.commonCode.OnlineManualTypeCode[0].key;
                // this.feeBasicInfo.feeAccountNoInputTypeCode = this.$store.state.CommonCodeModule.commonCode.FeeAccountNoInputTypeCode[0].key;
                // this.feeBasicInfo.feeCalcApplyBaseCode = this.$store.state.CommonCodeModule.commonCode.FeeCalcApplyBaseCode[0].key;
                this.feeBasicInfo.feeApplyStatusCode = this.$store.state.CommonCodeModule.commonCode.FeeApplyStatusCode[0].key;
                // this.feeBasicInfo.feeCalcTargetCode = this.$store.state.CommonCodeModule.commonCode.FeeCalcTargetCode[0].key;
                // this.feeBasicInfo.feeCalcMethodTypeCode = this.$store.state.CommonCodeModule.commonCode.FeeCalcMethodTypeCode[0].key;
                this.isDisabledField = false;
            } else {
                this.requestFeeInfoDetail();
            }
            this.feeBasicInfo.feeStartTime = {
                hours: Number(this.currentTime.trim().slice(0, 2)),
                minutes: Number(this.currentTime.trim().slice(3, 5)),
                seconds: Number(this.currentTime.trim().slice(6, 8))
            }
            this.feeBasicInfo.feeEndTime = {
                hours: Number(this.currentTime.trim().slice(0, 2)),
                minutes: Number(this.currentTime.trim().slice(3, 5)),
                seconds: Number(this.currentTime.trim().slice(6, 8))
            }
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const body = {
                        productFeeCode: authDetailParsed.productFeeCode,
                        feeCurrencyCode: authDetailParsed.feeCurrencyCode,
                        applyStartDate: authDetailParsed.applyStartDate,
                        applySeqNo: Number(authDetailParsed.applySeqNo)
                    };
                    const res: any = await this.$request.getRequest('PFM20100112', body, false);
                    const data = res.body;
                    const dateTimeFormatObj = {
                        applyStartDate: data.applyStartDate ? this.$datetime.convertStringToDate(data.applyStartDate) : "",
                        applyEndDate: data.applyEndDate ? this.$datetime.convertStringToDate(data.applyEndDate) : "",
                        feeStartTime: data.feeStartTime ? this.$datetime.convertTimeStringToTimeObject(data.feeStartTime) : this.feeBasicInfo.feeStartTime,
                        feeEndTime: data.feeEndTime ? this.$datetime.convertTimeStringToTimeObject(data.feeEndTime) : this.feeBasicInfo.feeEndTime,
                    }
                    this.feeBasicInfo = { ...data, ...dateTimeFormatObj };
                } else {
                    const data = authDetailParsed;
                    const dateTimeFormatObj = {
                        applyStartDate: data.applyStartDate ? this.$datetime.convertStringToDate(data.applyStartDate) : "",
                        applyEndDate: data.applyEndDate ? this.$datetime.convertStringToDate(data.applyEndDate) : "",
                        feeStartTime: data.feeStartTime ? this.$datetime.convertTimeStringToTimeObject(data.feeStartTime) : this.feeBasicInfo.feeStartTime,
                        feeEndTime: data.feeEndTime ? this.$datetime.convertTimeStringToTimeObject(data.feeEndTime) : this.feeBasicInfo.feeEndTime,
                    }
                    this.feeBasicInfo = { ...data, ...dateTimeFormatObj };
                }
            })
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        displayType() {
            return this.$route.query.type;
        },
        queryRoute() {
            return this.$route.query;
        },
        btnDelete(): boolean {
            if (this.displayType == 'edit' && this.isEdit) {
                return true;
            }
            return false;
        },
        btnCancel(): boolean {
            if (this.displayType == 'register' || this.isEdit == false) {
                return true;
            }
            return false;
        },
        btnList(): boolean {
            if (this.displayType == 'edit' && this.isEdit) {
                return true;
            }
            return false;
        },
        btnEdit(): boolean {
            if (this.displayType == 'edit' && this.isEdit) {
                return true;
            }
            return false;
        },
        formattedStartTime(): string {
            return this.$datetime.formatVueTimePicker(this.feeBasicInfo.feeStartTime)
        },
        formattedEndTime(): string {
            return this.$datetime.formatVueTimePicker(this.feeBasicInfo.feeEndTime)
        },
        formattedStartDate(): string {
            return this.$datetime.formatVueDatePicker(this.feeBasicInfo.applyStartDate)
        },
        formattedEndDate(): string {
            return this.$datetime.formatVueDatePicker(this.feeBasicInfo.applyEndDate)
        }
    },

    watch: {
        'feeBasicInfo.applyEndDate'(value: PFM201001SC00_RES_DETAIL) {
            if (value && this.$validate.startDateEndDateValidation(this.feeBasicInfo.applyStartDate, this.feeBasicInfo.applyEndDate) == true) {
                this.isInvalidDate = false;
            }
        },
        'feeBasicInfo.applyStartDate'(value: PFM201001SC00_RES_DETAIL) {
            if (value && this.$validate.startDateEndDateValidation(this.feeBasicInfo.applyStartDate, this.feeBasicInfo.applyEndDate) == true) {
                this.isInvalidDate = true;
            }
        },
        'feeBasicInfo.productFeeCode'(value: string) {
            this.isInvalidCode = this.$validate.validateWhiteSpace(value);
        },
    },

    methods: {
        async requestFeeInfoDetail() {
            const body = {
                productFeeCode: this.queryRoute.productFeeCode,
                feeCurrencyCode: this.queryRoute.feeCurrencyCode,
                applyStartDate: this.queryRoute.applyStartDate,
                applySeqNo: Number(this.queryRoute.applySeqNo)
            };
            const res: any = await this.$request.getRequest('PFM20100112', body, false);
            const data = res.body;
            const dateTimeFormatObj = {
                applyStartDate: data.applyStartDate ? this.$datetime.convertStringToDate(data.applyStartDate) : "",
                applyEndDate: data.applyEndDate ? this.$datetime.convertStringToDate(data.applyEndDate) : "",
                feeStartTime: data.feeStartTime ? this.$datetime.convertTimeStringToTimeObject(data.feeStartTime) : this.feeBasicInfo.feeStartTime,
                feeEndTime: data.feeEndTime ? this.$datetime.convertTimeStringToTimeObject(data.feeEndTime) : this.feeBasicInfo.feeEndTime,
            }
            this.feeBasicInfo = { ...data, ...dateTimeFormatObj };
            this.modifyCard = {
                registerTellerId: res.body.firstRegisterTellerID,
                registerTellerName: res.body.firstRegisterTellerName,
                registerDate: res.body.firstRegisterDate,
                registerTime: res.body.firstRegisterTime,
                updatedTellerId: this.feeBasicInfo.feeApplyStatusCode === '04' ? '' : res.body.lastChangeTellerID,
                updatedTellerName: this.feeBasicInfo.feeApplyStatusCode === '04' ? '' : res.body.lastChangeTellerName,
                updatedDate: this.feeBasicInfo.feeApplyStatusCode === '04' ? '' : res.body.lastChangeDate,
                updatedTime: this.feeBasicInfo.feeApplyStatusCode === '04' ? '' : res.body.lastChangeTime,
                deleteTellerId: this.feeBasicInfo.feeApplyStatusCode === '04' ? res.body.lastChangeTellerID : '',
                deleteTellerName: this.feeBasicInfo.feeApplyStatusCode === '04' ? res.body.lastChangeTellerName : '',
                deleteDate: this.feeBasicInfo.feeApplyStatusCode === '04' ? res.body.lastChangeDate : '',
                deleteTime: this.feeBasicInfo.feeApplyStatusCode === '04' ? res.body.lastChangeTime : '',
            };
        },
        async handleDeleteAction() {
            const body = {
                productFeeCode: this.feeBasicInfo.productFeeCode,
                feeCurrencyCode: this.feeBasicInfo.feeCurrencyCode,
                applyStartDate: this.$datetime.convertDateToString(this.feeBasicInfo.applyStartDate),
                applySeqNo: this.feeBasicInfo.applySeqNo,
            }
            await this.$request.getRequest('PFM20100141', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.$router.push('PFM201001SC00');
                        this.$dialog.onOpen({
                            content: this.$t("message.delete_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                modalController.dismiss()
                            }
                        });
                    }
                })
        },

        async handleRegister() {
            const body: PFM201001SC00_REQ_REGISTER = {
                productFeeCode: this.feeBasicInfo.productFeeCode,
                feeCurrencyCode: this.feeBasicInfo.feeCurrencyCode,
                applyStartDate: this.$datetime.convertDateToString(this.feeBasicInfo.applyStartDate),
                applySeqNo: Number(this.feeBasicInfo.applySeqNo),
                feeDescription: this.feeBasicInfo.feeDescription,
                feeStartTime: this.$datetime.convertTimeObjectToTimeString(this.feeBasicInfo.feeStartTime),
                feeEndTime: this.$datetime.convertTimeObjectToTimeString(this.feeBasicInfo.feeEndTime),
                applyEndDate: this.$datetime.convertDateToString(this.feeBasicInfo.applyEndDate),
                feeApplyStatusCode: this.feeBasicInfo.feeApplyStatusCode,
                productFeeTypeCode: this.feeBasicInfo.productFeeTypeCode,
                productFeeBusinessTypeCode: this.feeBasicInfo.productFeeBusinessTypeCode,
                onlineManualTypeCode: this.feeBasicInfo.onlineManualTypeCode,
                feeName: this.feeBasicInfo.feeName,
                feeShortName: this.feeBasicInfo.feeShortName,
                feeCalcTargetCode: this.feeBasicInfo.feeCalcTargetCode,
                feeCalcMethodTypeCode: this.feeBasicInfo.feeCalcMethodTypeCode,
                feeCalcTargetAmountTypeCode: this.feeBasicInfo.feeCalcTargetAmountTypeCode,
                feeCustomerInputTypeCode: this.feeBasicInfo.feeCustomerInputTypeCode,
                feeAccountNoInputTypeCode: this.feeBasicInfo.feeAccountNoInputTypeCode,
                feeCalcApplyBaseCode: this.feeBasicInfo.feeCalcApplyBaseCode,
                bSAccountCode: this.feeBasicInfo.bSAccountCode,
                iSAccountCode: this.feeBasicInfo.iSAccountCode
            };
            await this.$request.getRequest('PFM20100121', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.$router.push("PFM201001SC00");
                        this.$dialog.onOpen({
                            content: this.$t("message.register_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                modalController.dismiss()
                            }
                        });
                    }
                });
        },

        async handleEdit() {
            const body: PFM201001SC01_REQ_UPDATE = {
                productFeeCode: this.feeBasicInfo.productFeeCode,
                feeCurrencyCode: this.feeBasicInfo.feeCurrencyCode,
                applyStartDate: this.$datetime.convertDateToString(this.feeBasicInfo.applyStartDate),
                applySeqNo: Number(this.feeBasicInfo.applySeqNo),
                feeDescription: this.feeBasicInfo.feeDescription,
                feeStartTime: this.$datetime.convertTimeObjectToTimeString(this.feeBasicInfo.feeStartTime),
                feeEndTime: this.$datetime.convertTimeObjectToTimeString(this.feeBasicInfo.feeEndTime),
                applyEndDate: this.$datetime.convertDateToString(this.feeBasicInfo.applyEndDate),
                feeApplyStatusCode: this.feeBasicInfo.feeApplyStatusCode,
                productFeeTypeCode: this.feeBasicInfo.productFeeTypeCode,
                productFeeBusinessTypeCode: this.feeBasicInfo.productFeeBusinessTypeCode,
                onlineManualTypeCode: this.feeBasicInfo.onlineManualTypeCode,
                feeName: this.feeBasicInfo.feeName,
                feeShortName: this.feeBasicInfo.feeShortName,
                feeCalcTargetCode: this.feeBasicInfo.feeCalcTargetCode,
                feeCalcMethodTypeCode: this.feeBasicInfo.feeCalcMethodTypeCode,
                feeCalcTargetAmountTypeCode: this.feeBasicInfo.feeCalcTargetAmountTypeCode,
                feeCustomerInputTypeCode: this.feeBasicInfo.feeCustomerInputTypeCode,
                feeAccountNoInputTypeCode: this.feeBasicInfo.feeAccountNoInputTypeCode,
                feeCalcApplyBaseCode: this.feeBasicInfo.feeCalcApplyBaseCode,
                bSAccountCode: this.feeBasicInfo.bSAccountCode,
                iSAccountCode: this.feeBasicInfo.iSAccountCode
            };
            await this.$request.getRequest('PFM20100131', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.isDisabledField = true;
                        this.isEdit = true;
                        this.isMissingFiled = false;
                        this.requestFeeInfoDetail();
                        this.$dialog.onOpen({
                            content: this.$t("message.update_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                this.requestFeeInfoDetail()
                                modalController.dismiss()
                            }
                        });
                    }
                });
        },

        handleSave() {
            switch (this.$route.query.type) {
                case "register":
                    this.handleRegister();
                    break;
                case "edit":
                    this.handleEdit();
                    break;
            }
        },

        handleSaveAction() {
            if (
                !this.feeBasicInfo.productFeeCode ||
                !this.feeBasicInfo.productFeeTypeCode ||
                !this.feeBasicInfo.feeCurrencyCode ||
                (this.feeBasicInfo.applyStartDate.toString() == "Invalid Date" || this.feeBasicInfo.applyStartDate == null) ||
                !this.feeBasicInfo.onlineManualTypeCode ||
                !this.feeBasicInfo.feeName.trim() ||
                !this.feeBasicInfo.feeCalcMethodTypeCode ||
                (this.$validate.startDateEndDateValidation(this.feeBasicInfo.applyStartDate, this.feeBasicInfo.applyEndDate) == false)
            ) {
                this.isMissingFiled = true;
                this.isInvalidDate = true;
                this.$util.scrollToElement('inp_error')
            } else {
                this.$dialog.onOpen({
                    content: this.$t("message.would_you_like_to_save"),
                    dialogMode: "Question",
                    callback: () => {
                        modalController.dismiss();
                        this.handleSave();
                    }
                })
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case "edit":
                    this.isEdit = false;
                    this.isDisabledField = false;
                    break;
                case "list":
                    this.$router.push("PFM201001SC00");
                    break;
                case 'delete':
                    this.$dialog.onOpen({
                        content: this.$t("message.would_you_like_to_delete"),
                        dialogMode: "Question",
                        callback: () => {
                            modalController.dismiss();
                            this.handleDeleteAction();
                        },
                    });
                    break;
                case 'cancel':
                    if (this.displayType == 'edit' && this.isEdit == false) {
                        this.$dialog.onOpen({
                            content: this.$t("message.cancel_without_saving_changes"),
                            dialogMode: "Question",
                            callback: () => {
                                this.isDisabledField = true;
                                this.isEdit = true;
                                this.isMissingFiled = false;
                                this.requestFeeInfoDetail()
                                modalController.dismiss();
                            },
                        });
                    } else {
                        this.$dialog.onOpen({
                            content: this.$t("message.are_you_sure_you_want_to_cancel_registration?"),
                            dialogMode: "Question",
                            callback: () => {
                                modalController.dismiss();
                                this.$router.push("PFM201001SC00");
                            },
                        });
                    }
                    break;
            }
        },
    },
});
